@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');

@layer base {
    body {
    font-family: 'Raleway', sans-serif;
    }

    li {
        @apply px-4;
        @apply cursor-pointer;
    }
}

.content-div {
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    height: 250px;
}

.content-div:hover {
    text-align: center;
    background-image: linear-gradient(to left, rgba(112, 157, 255, 0.8),
    hsla(242, 74%, 61%, 0.8)) !important;
}